	module.exports = {"data":{"markdownRemark":{"html":"<!-- ![Pic Alt](./Graylog2.png) -->\n<p><strong>What is Markdown</strong>\nIt is a writing tool where you can write plain text and\nand then convert it into HTML code. It is a much more simple language than HTML\nThe syntax/code is easier and faster to type in.</p>\n<p><strong>So why use Markdown ?</strong></p>\n<ul>\n<li>\n<p>Easy to learn</p>\n</li>\n<li>\n<p>It is a easy to learn language, when you first get hold\non the syntax, you could try to take one of my text and\nwrite it again just in Mardown. if you can do that, then\nyou will be far more familiar with the syntax.\nyou can see the syntax <a href=\"https://github.com/adam-p/markdown-here/wiki/Markdown-Cheatsheet#links\">here</a></p>\n</li>\n<li>\n<p>Fast</p>\n</li>\n<li>\n<p>It is lighting fast to write in Markdown! you don't ever\nhave to move your hands from the keyboard to choose\na font or a special formattin!</p>\n</li>\n</ul>\n<ul>\n<li>Simple to read</li>\n<li>Markdown is a simple language, if you took a Markdown te-\ntext and hold it up again on a HTML code on 100 lines, then\nyou could easily see that it is way more easy to read markdown\nvs HTML.</li>\n</ul>\n<ul>\n<li>Flexible</li>\n<li>it is too! if your are missing something or forgot something\nwhen you are writing in Markdown then you can easily just add\na piece of HTML code in you markdown text.\nIt is also possible to convert Markdown to almost anything!\ndocx, mail, pdf you name it. You can use <a href=\"http://pandoc.org/\">this</a> tool to convert it</li>\n</ul>\n<ul>\n<li>Portable</li>\n<li>And at last it is also portable there is no limits when you are\nmooving back and forth between different types of Os'es.\nThe size of a markdown is also small! i think this document here\nis about 1,4kB! I don't think it could get any smaller.</li>\n</ul>\n<p>I will never ever look back again, i am in that belief that markdown is the way forward!\nthere are different programs you can write markdown in, there is the simple ones and the more advanced ones.\nBut i think that <a href=\"http://pad.haroopress.com/user.html\">Haoopad</a> is probably the best to start with because it as a\nsyntax cheatsheet in the left side of the screeen.</p>","frontmatter":{"path":"/08-24-2017-first-blog","title":"Why you should use markdown"}}},"pathContext":{}}

